<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="sign_from">
  <el-form-item label="" prop="username">
    <el-input v-model="ruleForm.username"></el-input>
  </el-form-item>
  <el-form-item label="" prop="nickname">
    <el-input v-model="ruleForm.nickname"></el-input>
  </el-form-item>
  <el-form-item label="" prop="password">
    <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="checkPass">
    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="email">
    <el-input v-model="ruleForm.email"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="success" @click="onSubmit"></el-button>
    <el-button @click="back" ></el-button>
  </el-form-item>
  </el-form>
</template>
<script>
  import {postRequest} from '../utils/api'
  import {putRequest} from '../utils/api'
  export default{
   data() {
        var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          username: '',
          nickname: '',
          password: '',
          checkPass:'',
          email: ''
        },
         rules: {
          username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 64, message: ' 3  64 ', trigger: 'blur' }
          ],
             nickname: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 64, message: ' 1  64 ', trigger: 'blur' }
          ],
          password: [
            { required: true,validator: validatePass, trigger: 'blur' },
            { min: 6, max: 64, message: ' 6  64 ', trigger: 'blur' }
          ],
          checkPass: [
            { required: true,validator: validatePass2, trigger: 'blur' },
            { min: 6, max: 64, message: ' 6  64 ', trigger: 'blur' }
          ],
          email:[
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: ['blur', 'change'] },
          ]
        }
      }
    },
    methods: {
      onSubmit:function() {
        var _this = this;
        this.loading = true;
        //
        //...
        postRequest('/reg', {
          username:  _this.ruleForm.username,
          password: _this.ruleForm.password,
          nickname: _this.ruleForm.nickname,
          email: _this.ruleForm.email
        }).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            //
            var json = resp.data;
            if (json.status == 'success') {
              _this.$alert('!', '!');
              _this.$router.replace({path: '/',query:{username:_this.ruleForm.username,password:_this.ruleForm.password}});
            } else {
              _this.$alert(json.msg, '!');
            }
          } else {
            //
            _this.$alert('!', '!');
          }
        }, resp=> {
          _this.loading = false;
          _this.$alert('!', '!');
        });
      },
      back:function(){
         var _this = this;
        _this.$router.replace({path: '/'});
      }
    }
  }
</script>
<style>
@import url("//unpkg.com/element-ui@2.8.2/lib/theme-chalk/index.css");

.sign_from{
    padding-left: 33%;
    height: 400px;
    width: 30%;
}
</style>



